const Buttons = ({ buttons, workoutResult, calMethods }) => {

    return (
        <div className="buttons">
            <div className="numbers">
            {buttons.map((value, index) => {
                return <div key={`${value}-${index}`} onClick={() => workoutResult(value)}>{value}</div>
            })}
            </div>
            <div className="methods">
            {calMethods.map((method, index) => {
                return <div key={`${method}-${index}`} onClick={() => workoutResult(method)}>{method}</div>
            })}
            </div>
        </div>
    )
}

export default Buttons;