import Screen from "./components/Screen";
import Buttons from "./components/Buttons";
import { useState } from "react";

const App = () => {
  const [buttons, setButtons] = useState(buttonValues);
  const [result, setResult] = useState("0");

  const [calMethods, setCalMethods] = useState(methods);

  const workoutResult = (value) => {
    if (result === "0") {
      setResult("");
    }

    if (typeof value === "number") {
        const regEx = /[^0-9]$/gim;
        if(result.match(regEx) && value == "0" && result.charAt(result.length-1) !== "."){
           console.log("no")
        }else{
            let num = value.toString();
            setResult((curState) => {
                return curState + num;
            });
        }
    } else if (value === "C") {
      setResult("0");
    } else if(value === "."){
        const regEx = /\d*\.\d*$/gmi;
        let onlyOneDecimalPoint = result;
        if(!onlyOneDecimalPoint.match(regEx)){
            setResult((curState) => {
                return curState + value;
              });
        }

    }else if (value === "=") {
      const regEx = /[^0-9]$/gim;
      let removeLastIfNan = result;
      while (removeLastIfNan.match(regEx)) {
        removeLastIfNan = removeLastIfNan.substring(
          0,
          removeLastIfNan.length - 1
        );
      }

      const solution = eval(removeLastIfNan);

      setResult(`${solution}`);
    } else {
      const regEx = /[^0-9]$/gim;

      if (!result.match(regEx)) {
        setResult(`${result}${value}`);
      }
    }
  };

  return (
    <div className="table">
      <div className="calBody">
        <Screen result={result} />
        <Buttons
          buttons={buttons}
          workoutResult={workoutResult}
          setResult={setResult}
          calMethods={calMethods}
        />
      </div>
    </div>
  );
};

export default App;

const buttonValues = [9, 8, 7, 6, 5, 4, 3, 2, 1, 0, ".", "C"];

const methods = ["+", "-", "/", "*", "="];
